.container {

    position: relative;
    margin: 0 auto;
    width: 414px;
    height: 899px;
    left: -1px;
    top: -3px;
    background: #FBFBFD;
}


.img-success {

    position: absolute;
    width: 100px;
    height: 100px;
    left: 163px;
    top: 141px;
    background: url('../../asset/image\ 12.png');
    filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.1));
    
}

.status {
    position: absolute;
    width: 328px;
    height: 48px;
    left: 44px;
    top: 252px;

    font-family: 'SF UI Text';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    /* or 133% */

    text-align: center;
    letter-spacing: -0.5px;

    color: #636363;
}

.comment {

    position: absolute;
    width: 235px;
    height: 29px;
    left: 90px;
    top: 277px;

    font-family: 'SF UI Text';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    text-align: center;

    color: #AEAEAE;
}


.login-again {
    /* Rectangle 70 */


    position: absolute;
    width: 325px;
    height: 54px;
    left: 45px;
    top: 328px;

    background: #2CD1F8;
    border-radius: 8px;
    outline: none;
    border: none;
    font-family: 'SF UI Text';
font-style: normal;
font-weight: 700;
font-size: 18px;
line-height: 18px;
/* identical to box height, or 100% */


color: #FFFFFF;
}